import * as React from 'react';
import './App.css';

interface AppProps {

}

type Player = 'O' | 'X'

interface AppState {
  list: string[]
  next: Player
  done: boolean
  winner: string | null,
  last: string[] | null
}

class App extends React.Component<AppProps, AppState> {
  last: string[] | null
  constructor(props: any) {
    super(props)
    this.state = {
      list: [
        '', '', '',
        '', '', '',
        '', '', ''
      ],
      next: 'O',
      done: false,
      winner: null,
      last: null
    },
    this.clickHandle = this.clickHandle.bind(this)
    this.goBack = this.goBack.bind(this)
    this.restart = this.restart.bind(this)
  }

  clickHandle(targetIndex: number) {
    if (this.state.done) {
      return
    }
    let _list = this.state.list
    let _next = this.state.next
    let _last = [...this.state.list]
    _list[targetIndex] = this.state.next
    _next = _next === 'O' ? 'X' : 'O'
    this.setState({
      list: [..._list],
      next: _next,
      last: _last
    })
    this.judgeWiner()
  }

  goBack() {
    if (this.state.last) {
      this.setState({
        list: this.state.last,
        next: this.state.next === 'O' ? 'X' : 'O',
        last: null
      })
    }
  }

  judgeWiner() {
    let winList: number[][] = [
      [1,2,3],
      [4,5,6],
      [7,8,9],
      [1,5,9],
      [3,5,7],
      [1,4,7],
      [2,5,8],
      [3,6,9]
    ]

    let { list } = this.state

    for (let map of winList) {
      let a = list[map[0] - 1]
      let b = list[map[1] - 1]
      let c = list[map[2] - 1]
      if (a && b && c && (a === b && b === c)) {
        this.setState({
          done: true,
          winner: list[map[0] - 1]
        })
      }
    }
  }

  restart() {
    this.setState({
      list: [
        '', '', '',
        '', '', '',
        '', '', ''
      ],
      next: 'O',
      done: false,
      winner: null
    })
  }

  render() {
    const { list, next, last, done, winner } = this.state
    return (
      <div className='playground'>
        <div className='list'>
          {list.map((v, i) => Items(v, i, () => { this.clickHandle(i) }))}
        </div>
        {!done && <p>next player: {next}</p>}
        {last && !done &&  <button onClick={this.goBack}></button>}
        {done && <p className='show__winner'>{winner}</p>}
        {done && <button onClick={this.restart}></button>}
      </div>
    )
  }
}

function Items (item: string, index: number, clickHandle: any) {
  return <li key={item + index.toString()} className='list__item' onClick={clickHandle}> {item} </li>
}

export default App;




